<template>
    <div class="home">
        <div class="djtIndex-top">
            <img src="../../assets/images/kuang-back.png" alt="" class="djtIndex-top-back">
            <div class="djtIndex-top-mean">
                <div class="djtIndex-top-mean-left">
                    <img src="../../assets/images/logo.png" alt="" class="djtIndex-top-mean-left-img">
                    <span class="djtIndex-top-mean-left-text">Donald John Trump</span>
                </div>
                <div class="djtIndex-top-mean-right">
                    <span class="djtIndex-top-mean-right-text" @click="showMask()">链接钱包</span>
                    <img src="../../assets/images/index-lang.png" alt="" class="djtIndex-top-mean-right-img1" @click="showLang()">
                    <img src="../../assets/images/index-mean.png" alt="" class="djtIndex-top-mean-right-img2" @click="showMean">
                </div>
            </div>
            <div class="kuang-content">
                <div class="djtIndex-content-title">正式挖矿</div>
                <div class="kuang-content-desc">
                    开启后，DJT 池为质押池，挖矿剩余：23333 枚 （总量 70%）质押池 7 周（49 天）的挖矿时间， 每 10 分钟作为一个单位时间发放挖矿收益，每周减产。
                </div>
                <div class="kuang-content-detail" @click="showKuang">
                    挖矿详情 >
                </div>
                <div class="djtIndex-content-title">DJT挖矿</div>
                <div class="kuang-content-number">
                    挖矿数量
                </div>
                <div>
                    <input type="text" placeholder="请输入数量" class="kuang-content-input" />
                </div>
                <div class="kuang-content-input-desc">需质押DJT数量739，USDT数量281</div>
                <div class="kuang-content-number">
                    挖矿时间
                </div>
                <div class="kuang-content-button">
                    <div class="kuang-content-button1">25天</div>
                    <div class="kuang-content-button2">49天</div>
                </div>
                <div class="kuang-content-submit">开始挖矿</div>
            </div>
        </div>

        <div v-if="meanShow" class="index_pop_content">
            <div class="index_pop_top">
                <div>
                    <img src="../../assets/images/logo.png" alt="" class="index_pop_top-left">
                </div>
                <div @click="closeMean">
                    <img src="../../assets/images/close.png" alt="" class="index_pop_top-right">
                </div>
            </div>
            <div class="index_pop_main_tab">
                <div class="index_pop_middle"  @click="toIndex()">
                    <div class="index_pop_middle_left">
                        <img src="../../assets/images/index-home.png" alt="">
                        <span>首页</span>
                    </div>
                </div>
                <div class="index_pop_middle">
                    <div class="index_pop_middle_left">
                        <img src="../../assets/images/duihuan.png" alt="">
                        <span>交换DJT</span>
                    </div>
                </div>
                <div class="index_pop_middle" @click="toKuang()">
                    <div class="index_pop_middle_left">
                        <img src="../../assets/images/index-kuang.png" alt="">
                        <span>DJT挖矿</span>
                    </div>
                </div>
                <div class="index_pop_middle" @click="toOrder">
                    <div class="index_pop_middle_left">
                        <img src="../../assets/images/index-record.png" alt="">
                        <span>挖矿记录</span>
                    </div>
                </div>
                <div class="index_pop_middle" @click="toWhite">
                    <div class="index_pop_middle_left">
                        <img src="../../assets/images/index-white.png" alt="">
                        <span>白皮书</span>
                    </div>
                </div>
                <div class="index_pop_bottom">
                    <div class="index_pop_bottom-left"><img src="../../assets/images/Twitte.png" alt=""></div>
                    <div class="index_pop_bottom-left"><img src="../../assets/images/dianbao.png" alt=""></div>
                </div>
            </div>
        </div>
        <div v-if="maskShow" class="index_mask">
            <div class="index_mask-content">
                <div class="index_mask-title">链接钱包</div>
                <img src="../../assets/images/matemask.png" alt="" class="index_mask-img">
            </div>
            <div @click="closeMask()">
                <img src="../../assets/images/closed.png" alt="" class="index_mask-close">
            </div>
        </div>
        <div v-if="langShow" class="index_lang">
            <div class="index_lang-button">中文</div>
            <div class="index_lang-button-active"> EN</div>
        </div>
        <div v-if="kuangShow" class="index_kuang">
            <div class="index_kuang-content">
                <div class="index_kuang-title">DJT 质押池</div>
                <div class="index_kuang-desc">
                    第一周 26%（6066 枚，每十分钟=6.017857 枚）<br/> <br/>
                    第二周 22%（5133 枚，每十分钟=5.0922619 枚）<br/> <br/>
                    第三周 16%（3733 枚，每十分钟=3.70337302 枚） <br/> <br/>
                    第四周 13%（3033 枚，每十分钟=3.00892857 枚） <br/> <br/>
                    第五周 10%（2333 枚，每十分钟=2.31448413 枚） <br/> <br/>
                    第六周 8%（1867 枚，每十分钟=1.85218254 枚） <br/> <br/>
                    第七周 5%（1167 枚，每十分钟=1.1577381 枚）
                </div>
            </div>
            <div @click="closeKuang()">
                <img src="../../assets/images/closed.png" alt="" class="index_mask-close">
            </div>
        </div>
    </div>
</template>
<script>
    import {WOW} from 'wowjs';
    let provider, signer, defaultAccount, networkVersion;
    let baseUrl = "";
    export default {
        data() {
            return {
                lang:'zh',
                lang_list:{},
                langShow:false,
                address: '',
                short_address: 'connect',
                parents:'',
                meanShow: false,
                maskShow:false,
                kuangShow:false,
            }
        },
        created: function () {

        },
        async mounted() {
            this.lang = sessionStorage.getItem('lang')?sessionStorage.getItem('lang'):'en';
            this.lang_list = this.en.lang;
            switch(this.lang){
                case 'zh':
                    this.lang_list = this.zh.lang;
                    break;
                case 'en':
                    this.lang_list = this.en.lang;
                    break;
            }
            this.parents = this.$route.query.p_address?this.$route.query.p_address:this.getUrlParam('p_address');
            this.checkNet();
            var wow = new WOW(
                {
                    boxClass: 'wow',      // animated element css class (default is wow)
                    animateClass: 'animated', // animation css class (default is animated)
                    offset: 0,          // distance to the element when triggering the animation (default is 0)
                    mobile: true,       // trigger animations on mobile devices (default is true)
                    live: true,       // act on asynchronously loaded content (default is true)
                    callback: function (box) {
                        // the callback is fired every time an animation is started
                        // the argument that is passed in is the DOM node being animated
                    },
                    scrollContainer: null // optional scroll container selector, otherwise use window
                }
            );
            wow.init();
        },
        methods: {
            toWhite(){
                window.open('/static/djt.pdf');
            },
            closeKuang(){
                this.kuangShow = false;
            },
            showKuang(){
                this.kuangShow = true;
            },
            toIndex(){
                this.$router.push({
                    name: 'home',
                    params: {

                    }
                })
                this.meanShow = false;
            },
            toOrder(){
                this.$router.push({
                    name: 'order',
                    params: {

                    }
                })
                this.meanShow = false;
            },
            toKuang(){
                this.$router.push({
                    name: 'kuang',
                    params: {

                    }
                })
                this.meanShow = false;
            },
            switchLang(type,event){
                let toggle = event.currentTarget;
                switch(type){
                    case 'zh':
                        sessionStorage.setItem('lang','zh');
                        break;
                    case 'en':
                        sessionStorage.setItem('lang','en');
                        break;
                }
                location.reload();
            },
            //显示菜单
            showLang(){
                if(this.langShow){
                    this.langShow = false;
                }else{
                    this.langShow = true;
                }

            },
            //显示菜单
            showMask(){
                this.maskShow = true;
            },
            //关闭菜单
            closeMask(){
                this.maskShow = false;
            },
            //显示菜单
            showMean(){
                this.meanShow = true;
            },
            //关闭菜单
            closeMean(){
                this.meanShow = false;
            },
            connectWallet() {
                if (this.address) {
                    this.short_address = this.address.substr(0, 3) + '...' + this.address.substr(this.address.length - 3)
                } else {
                    this.checkNet()
                }
            },
            getUrlParam(name) {
                var reg = new RegExp("(^|&)" + name + "=([^&]*)(&|$)"); //构造一个含有目标参数的正则表达式对象
                var r = window.location.search.substr(1).match(reg);  //匹配目标参数
                if (r != null) return unescape(r[2]);
                return null; //返回参数值
            },
            async checkNet() {
                let _this = this;

                try {
                    provider = new ethers.providers.Web3Provider(web3.currentProvider);
                    signer = provider.getSigner();
                    _this.checkLogin();
                } catch (e) {
                    _this.$toast('请使用币安只能链连接');
                }

            },
            async checkLogin() {
                let _this = this;
                setInterval(function () {
                    window.ethereum.enable()
                        .then(function (accounts) {
                            if (accounts.length == 0 && defaultAccount != "") {
                                window.location.reload();
                                return;
                            }
                            if (accounts.length == 0) {
                                window.location.reload();
                                return;
                            }
                            if (accounts[0] != defaultAccount) {
                                defaultAccount = accounts[0];
                                window.location.reload();
                                console.log("account change, start bat updateData!")
                            }
                        })
                        .catch(function (reason) {
                            // Handle error. Likely the user rejected the login:
                            console.log(reason === "User rejected provider access")
                        })
                }, 1000);
                window.ethereum.enable()
                    .then(async function (accounts) {
                        if (accounts.length == 0) {
                            console.log('33333');
                            return;
                        }
                        defaultAccount = accounts[0];
                        console.log("defaultAccount: ", defaultAccount);
                        networkVersion = window.ethereum.networkVersion
                        if (networkVersion == 1) {  //以太坊主网
                            _this.type = 'ETH';
                            _this.$toast('请使用币安只能链连接');
                            return false;
                        }
                        else if (networkVersion == 42) {   //以太坊测试网  K
                            _this.type = 'ETH';
                            _this.$toast('请使用币安只能链连接');
                            return false;
                        }
                        else if (networkVersion == 128) {  //HECO主网
                            _this.type = 'HT';
                            _this.$toast('请使用币安只能链连接');
                            return false;
                        }
                        else if (networkVersion == 256) { //HECO测试网
                            _this.type = 'HT';
                            _this.$toast('请使用币安只能链连接');
                            return false;
                        }
                        else if (networkVersion == 56) { //币安主网
                            _this.type = 'BNB';
                        }
                        else {
                            _this.$toast('请使用币安只能链连接');
                            return false;
                        }
                        if (defaultAccount != '') {
                            _this.address = defaultAccount;
                        }
                    });
                setTimeout(function () {
                    if (defaultAccount) {
                        _this.address = defaultAccount;
                        _this.short_address = _this.address.substr(0, 3) + '...' + _this.address.substr(_this.address.length - 3)
                    }

                }, 500)
            },
        }
    }
</script>


